<template>
	<view class="page">

		<img class="haibao_image" :src="poster_url" mode="aspectFill"></img>
		<!-- <image class="haibao" :src="poster_url"></image> -->
		<view class="extension_poster-add" @click="addPoster">{{savetitle}}</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				poster_url: '',
				courseid: '',
				'savetitle': '保存海报'
			}
		},
		onReady() {

			// #ifdef H5
			this.savetitle = "长按海报保存到相册";
			// #endif

		},
		onLoad(option) {
			console.log(option);

			this.myname = getApp().globalData.userinfo.user_nickname;
			this.myavatar = getApp().globalData.userinfo.avatar;
			var type = option.type;
			var Agent = '';
			if (type == 1) {
				uni.setNavigationBarTitle({
					title:'海报分享'
				});
				//课程
				Agent = getApp().globalData.site_url + 'App.Agent.GetShareImage';
			} else if (type == 2) {
				uni.setNavigationBarTitle({
					title:'海报分享'
				});
				//套餐
				Agent = getApp().globalData.site_url + 'App.Agent.GetShareImage';
			} else {
				uni.setNavigationBarTitle({
					title:'海报推广'
				});
				//推广
				Agent = getApp().globalData.site_url + 'App.Agent.GetPopuImage';
			}


			var that = this;
			setTimeout(() => {
				uni.request({
					url: Agent,
					method: 'POST',
					data: {
						'courseid': option.courseid,
						'type': option.type,
						'uid': getApp().globalData.userinfo.id,
						'token': getApp().globalData.userinfo.token
					},
					success: res => {
						if (res.data.data.code == 700) {
							uni.navigateTo({
								url: '../login/login'
							})
							return;
						};
						this.poster_url = res.data.data.info.poster_url;
						console.log(this.poster_url);
						// console.log(JSON.parse(JSON.stringify(res)));
					}
				});
			}, 0);
		},
		methods: {
			addPoster() {
				// #ifndef H5
				uni.showLoading({
					title: '下载中...',
					mask: false
				});
				uni.downloadFile({
					url: this.poster_url,
					success: (res) => {
						uni.hideLoading();
						console.log(res);
						if (res.statusCode === 200) {
							uni.saveImageToPhotosAlbum({
								filePath: res.tempFilePath,
								success: (res) => {
									console.log(res);
									uni.showToast({
										title: '保存成功',
										icon: 'success'
									});
								},
								fail(res) {
									uni.showToast({
										title: '保存失败,请重试',
										icon: 'none'
									});
									console.log(res);
								}
							});
						}else{
							uni.hideLoading();
							uni.showToast({
								title: '下载失败',
								icon:'none'
							});
						}
					},fail:(res)=>{
						uni.hideLoading();
						uni.showToast({
							title: '下载失败',
							icon:'none'
						});
					}
				});
				// #endif
			}
		},
	}
</script>

<style>
	.page {
		width: 100%;
		height: 100%;
	}

	page {
		width: 100%;
		height: 100%;
		background-color: #F5F5F5;
		overflow: hidden;
	}

	.haibao_image {
		margin-top: 20rpx;
		position: absolute;
		left: calc(10%);
		right: 20rpx;
		height: 80%;
		width: 80%;
		border-radius: 16rpx;
	}

	.extension_poster-add {
		position: absolute;
		bottom: 30rpx;
		width: 80%;
		left: calc(10%);
		margin: 0 auto;
		text-align: center;
		margin-top: 20rpx;
		background: #38DAA6;
		color: #fff;
		height: 40px;
		border-radius: 50px;
		font-size: 16px;
		line-height: 40px;
	}
</style>
